import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { Collapse, Alert } from "@mui/material";

function ViewProgramPage() {
  const [program, setProgram] = useState({});
  const [deleted, setDeleted] = useState(false);
  const [restored, setRestored] = useState(false);
  const params = useParams();

  useEffect(() => {
    fetch("/api/v1/programs/" + params.id)
      .then((response) => response.json())
      .then((jsonResponse) => setProgram(jsonResponse));
  }, [params.id]);

  const handleDelete = () => {
    fetch(`/api/v1/programs/delete/` + params.id, {
      method: "PATCH",
    })
      .then((response) => response.json())
      .then((jsonResponse) => setProgram(jsonResponse));
    setDeleted(true);
    setRestored(false);
    setTimeout(() => {
      setDeleted(false);
             }, 5000);
  };

  const handleRestore = () => {
    fetch(`/api/v1/programs/restore/` + params.id, {
      method: "PATCH",
    })
      .then((response) => response.json())
      .then((jsonResponse) => setProgram(jsonResponse));
    setRestored(true);
    setDeleted(false);
    setTimeout(() => {
      setRestored(false);
             }, 5000);
  };

  return (
    <div className="mx-3">
      <h2 className="my-5">Programa</h2>

      <Collapse in={deleted}>
        <Alert
          onClose={() => {
            setDeleted(false);
          }}
          severity="info"
          className="mb-3"
        >
          raas skmingai itrintas
        </Alert>
      </Collapse>

      <Collapse in={restored}>
        <Alert
          onClose={() => {
            setRestored(false);
          }}
          severity="success"
          className="mb-3"
        >
          raas skmingai atstatytas
        </Alert>
      </Collapse>

      <div className="">
        <table className="table table-hover shadow p-3 mb-5 bg-body rounded align-middle">
          <tbody>
            <tr>
              <th scope="col">Pavadinimas</th>
              <td colSpan={4}>{program.name}</td>
            </tr>
            <tr>
              <th scope="col">Apraymas</th>
              <td colSpan={4}>{program.description}</td>
            </tr>
            <tr>
              <th scope="col">Bsena</th>
              <td colSpan={4}>{program.deleted ? "Itrintas" : "Aktyvus"}</td>
            </tr>
            <tr>
              <th scope="col">Paskutin kart modifikuotas</th>
              <td colSpan={4}>{program.modifiedDate}</td>
            </tr>
            <tr>
              <th rowSpan="0">Dalykai</th>
            </tr>
            {program.subjects?.map((subject) => (
              <tr
                className={subject.subject.deleted ? "text-black-50" : ""}
                key={subject.subject.id}
              >
                <td>{subject.subject.name}</td>
                <td>{subject.hours} valand</td>
                {subject.subject.deleted ? (
                  <td>Itrintas</td>
                ) : (
                  <td></td>
                )}
              </tr>
            ))}
          </tbody>
        </table>
        <button className="btn btn-primary me-2" disabled={program.deleted}>
          <Link to={"/programs/edit/" + params.id} className="nav-link">
            Redaguoti
          </Link>
        </button>
        {program.deleted ? (
          <button className="btn btn-secondary me-2" onClick={handleRestore}>
            Atstatyti
          </button>
        ) : (
          <button className="btn btn-danger me-2" onClick={handleDelete}>
            Itrinti
          </button>
        )}
      </div>
    </div>
  );
}

export default ViewProgramPage;
